import { Directive, ElementRef } from '@angular/core';
declare var jQuery: any;

@Directive ({
  selector: '[live-tile]'
})

export class LiveTile {
  $el: any;

  constructor(el: ElementRef) {
    this.$el = jQuery(el.nativeElement);
  }

  ngOnInit(): void {
    this.$el
      .css('height', this.$el.data('height'))
      .liveTile();
  }
}



